import React, {useState} from 'react';
import style from './Paginator.module.css';

const Paginator = ({totalItemsCount, pageSize, pageCurrent, onPageChange, portionSize, ...props}) => {
    let pagesCount = Math.ceil(totalItemsCount / pageSize); //подсчитываем кол во страниц, округляем до целого числа

    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }
    let portionCount = Math.ceil(pagesCount / portionSize);  // размер порции
    let [portionNumber, setPortionNumber] = useState(1);

    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1; // левая граница порции
    let rightPortionPageNumber = portionNumber * portionSize; // правая гроаница порции

    return (
        <div className={style.pagination_wrapper}>
            {portionNumber > 1 &&
            <button onClick={() => {
                setPortionNumber(portionNumber - 1)
            }}>back</button>}
            <ul className={style.pagination_list}>
                {pages
                    .filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
                    .map(p => {
                            return <li key={p} className={style.pagination_item}>
                                <a className={pageCurrent === p ? style.active : null} onClick={() => {
                                    onPageChange(p)
                                }}>{p}</a></li>
                        }
                    )}
            </ul>
            {portionCount > portionNumber &&
            <button onClick={() => {
                setPortionNumber(portionNumber + 1)
            }}>next</button>}
        </div>
    )
};

export default Paginator;